import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Index from './views/index.vue'
import Main from './views/layouts/main.vue'
import Invest from './views/invest.vue'
import Master from './views/master.vue'
import User from './views/user.vue'
import Article from './views/article/index.vue'
import ArticleAdd from './views/article/add.vue'

let routes = [

    {
        path: '/',
        component: Main,
        name:'',
        children: [
          { path: '/', component: Index, name: '', hidden: true },
          { path: '/article', component: Article, name: '', hidden: true },
          { path: '/master', component: Master, name: 'Master', hidden: true },
          { path: '/invest', component: Invest, name: '', hidden: true },
          { path: '/user', component: User, name: '', hidden: true },
          { path: '/article/add', component: ArticleAdd, name: '', hidden: true },
        ],
    },
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    //{ path: '/main', component: Main },
    /*
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
            { path: '/main', component: Main, name: '', hidden: true },
            { path: '/table', component: Table, name: 'Table' },
            { path: '/form', component: Form, name: 'Form' },
            { path: '/user', component: user, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/page4', component: Page4, name: '4' },
            { path: '/page5', component: Page5, name: '5' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-address-card',
        leaf: true,//
        children: [
            { path: '/page6', component: Page6, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: 'Charts',
        iconCls: 'fa fa-bar-chart',
        children: [
            { path: '/echarts', component: echarts, name: 'echarts' }
        ]
    },
    */
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;



