<template>
  <div>
    <!--id-->
    <script :id="randomId"  type="text/plain"></script>
  </div>
</template>
<script>
import '../../static/plugins/editor/ueditor.config.js'
import '../../static/plugins/editor/ueditor.all.min.js'

export default {
   props: {
     //
     ueditorConfig:{

     }
   },
   data () {
     return {
       //id,
       randomId: 'editor_' + (Math.random() * 100000000000000000),
       //
       instance: null,
     };
   },
   //--el,
   mounted () {
     this.initEditor()
   },

   beforeDestroy () {
     //  UEditor 
     if (this.instance !== null && this.instance.destroy) {
       this.instance.destroy();
     }
   },
   methods: {
     initEditor () {
       //dom
         this.$nextTick(() => {

           this.instance = UE.getEditor(this.randomId, {
           toolbars: [

    ['fullscreen', 'source', 'undo', 'redo','bold', 'italic', 'underline', 'fontborder', 'strikethrough', 'superscript', 'subscript', 'removeformat', 'formatmatch', 'autotypeset', 'blockquote', 'pasteplain', '|', 'forecolor', 'backcolor', 'insertorderedlist', 'insertunorderedlist', 'selectall', 'cleardoc']
],
initialFrameHeight:200,
           });
           //  UEditor  ready 
           this.instance.addListener('ready', () => {
             this.$emit('ready', this.instance);
           });
         });
       }
   }
 };
</script>



